import axios from "@/api/axios";

const AllClients = () => {
  return axios.get("/api/1/admin/get/all");
};
const getSearchClients = (serial) => {
  return axios.get("/api/1/admin/search/serial", {
    params: { serial: serial },
  });
};
const getSearchPhoneClient = (phone) => {
  return axios.get("/api/1/admin/search/phone", {
    params: { phone: phone },
  });
};
const deleteClient = (path) => {
  return axios.delete(`/api/1/admin/delete/${path.id}`);
};
const deleteWeaponClient = (user) => {
  console.log(user);
  return axios.delete(`/api/1/unit/delete`, { data: user });
};

export default {
  AllClients,
  deleteClient,
  getSearchClients,
  getSearchPhoneClient,
  deleteWeaponClient,
};
